import { LitElement, html, css } from 'lit';
import { customElement, property, state } from 'lit/decorators.js';
import { API_ENDPOINTS } from '../../config/api';
import { sharedStyles } from './shared-styles';

@customElement('eligibility-section')
export class EligibilitySection extends LitElement {
    static styles = [
        sharedStyles,
        css`
            :host {
                display: block;
            }

            .eligibility-status {
                align-items: center;
                background-color: var(--gray-50);
                border: 1px solid var(--gray-200);
                border-radius: 6px;
                display: flex;
                gap: 1rem;
                margin-bottom: 1.5rem;
                padding: 1rem;
            }

            .status-icon {
                font-size: 2rem;
            }

            .status-content {
                flex: 1;
            }

            .status-title {
                color: var(--gray-900);
                font-size: 1.125rem;
                font-weight: 500;
                margin-bottom: 0.25rem;
            }

            .status-message {
                color: var(--gray-600);
                font-size: 0.875rem;
            }

            .coverage-details {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
                gap: 1.5rem;
                margin-bottom: 1.5rem;
            }

            .coverage-card {
                background-color: white;
                border: 1px solid var(--gray-200);
                border-radius: 6px;
                padding: 1rem;
            }

            .coverage-header {
                align-items: center;
                display: flex;
                gap: 0.5rem;
                margin-bottom: 1rem;
            }

            .coverage-icon {
                color: var(--primary);
                font-size: 1.25rem;
            }

            .coverage-title {
                color: var(--gray-900);
                font-size: 1rem;
                font-weight: 500;
            }

            .coverage-grid {
                display: grid;
                gap: 0.75rem;
            }

            .coverage-item {
                display: flex;
                justify-content: space-between;
            }

            .item-label {
                color: var(--gray-600);
                font-size: 0.875rem;
            }

            .item-value {
                color: var(--gray-900);
                font-size: 0.875rem;
                font-weight: 500;
            }

            .benefits-list {
                border: 1px solid var(--gray-200);
                border-radius: 6px;
                margin-top: 1rem;
                overflow: hidden;
            }

            .benefit-item {
                align-items: center;
                border-bottom: 1px solid var(--gray-200);
                display: flex;
                justify-content: space-between;
                padding: 0.75rem 1rem;
            }

            .benefit-item:last-child {
                border-bottom: none;
            }

            .benefit-name {
                color: var(--gray-800);
                font-size: 0.875rem;
            }

            .benefit-value {
                color: var(--gray-900);
                font-size: 0.875rem;
                font-weight: 500;
            }

            .check-eligibility-btn {
                margin-bottom: 1.5rem;
                width: 100%;
            }

            .loading-overlay {
                align-items: center;
                background-color: rgba(255, 255, 255, 0.9);
                display: flex;
                height: 100%;
                justify-content: center;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
                z-index: 10;
            }

            .loading-content {
                text-align: center;
            }

            .loading-text {
                color: var(--gray-600);
                margin-top: 1rem;
            }

            .error-state {
                background-color: var(--error);
                color: white;
            }

            .success-state {
                background-color: var(--success);
                color: white;
            }

            .warning-state {
                background-color: var(--warning);
                color: white;
            }
        `
    ];

    static get properties() {
        return {
            eligibility: { type: Object },
            coverage: { type: Object },
            patient: { type: Object },
            visit: { type: Object },
            isLoading: { type: Boolean, state: true },
            error: { type: String, state: true }
        };
    }

    constructor() {
        super();
        this.eligibility = null;
        this.coverage = null;
        this.patient = null;
        this.visit = null;
        this.isLoading = false;
        this.error = null;
    }

    render() {
        return html`
            <div class="form-section">
                <h2 class="section-title">Eligibility & Coverage</h2>

                ${this.isLoading ? html`
                    <div class="loading-overlay">
                        <div class="loading-content">
                            <div class="loader"></div>
                            <p class="loading-text">Checking eligibility...</p>
                        </div>
                    </div>
                ` : ''}

                ${!this.eligibility ? html`
                    <button class="button button-primary check-eligibility-btn"
                            ?disabled=${!this.patient || !this.visit}
                            @click=${this.checkEligibility}>
                        Check Eligibility
                    </button>
                ` : ''}

                ${this.error ? html`
                    <div class="eligibility-status error-state">
                        <div class="status-icon"></div>
                        <div class="status-content">
                            <div class="status-title">Error Checking Eligibility</div>
                            <div class="status-message">${this.error}</div>
                        </div>
                    </div>
                ` : this.eligibility ? html`
                    <div class="eligibility-status ${this.getStatusClass(this.eligibility.status)}">
                        <div class="status-icon">${this.getStatusIcon(this.eligibility.status)}</div>
                        <div class="status-content">
                            <div class="status-title">${this.getStatusTitle(this.eligibility.status)}</div>
                            <div class="status-message">${this.eligibility.message}</div>
                        </div>
                    </div>

                    ${this.coverage ? html`
                        <div class="coverage-details">
                            <div class="coverage-card">
                                <div class="coverage-header">
                                    <span class="coverage-icon"></span>
                                    <span class="coverage-title">Insurance Details</span>
                                </div>
                                <div class="coverage-grid">
                                    <div class="coverage-item">
                                        <span class="item-label">Insurance Provider</span>
                                        <span class="item-value">${this.coverage.provider}</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Policy Number</span>
                                        <span class="item-value">${this.coverage.policyNumber}</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Plan Type</span>
                                        <span class="item-value">${this.coverage.planType}</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Effective Date</span>
                                        <span class="item-value">${this.formatDate(this.coverage.effectiveDate)}</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Expiry Date</span>
                                        <span class="item-value">${this.formatDate(this.coverage.expiryDate)}</span>
                                    </div>
                                </div>
                            </div>

                            <div class="coverage-card">
                                <div class="coverage-header">
                                    <span class="coverage-icon"></span>
                                    <span class="coverage-title">Coverage Details</span>
                                </div>
                                <div class="coverage-grid">
                                    <div class="coverage-item">
                                        <span class="item-label">Coverage Type</span>
                                        <span class="item-value">${this.coverage.coverageType}</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Network Type</span>
                                        <span class="item-value">${this.coverage.networkType}</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Co-Payment</span>
                                        <span class="item-value">${this.coverage.copayment}%</span>
                                    </div>
                                    <div class="coverage-item">
                                        <span class="item-label">Deductible</span>
                                        <span class="item-value">SAR ${this.coverage.deductible}</span>
                                    </div>
                                </div>

                                <div class="benefits-list">
                                    ${this.coverage.benefits.map(benefit => html`
                                        <div class="benefit-item">
                                            <span class="benefit-name">${benefit.name}</span>
                                            <span class="benefit-value">${benefit.value}</span>
                                        </div>
                                    `)}
                                </div>
                            </div>
                        </div>
                    ` : ''}
                ` : ''}
            </div>
        `;
    }

    async checkEligibility() {
        if (!this.patient || !this.visit) {
            this.error = 'Please select a patient and visit first';
            return;
        }

        this.isLoading = true;
        this.error = null;

        try {
            const response = await fetch(API_ENDPOINTS.ELIGIBILITY.CHECK, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    patientId: this.patient.id,
                    visitId: this.visit.id
                })
            });

            if (!response.ok) throw new Error('Failed to check eligibility');
            
            const data = await response.json();
            
            if (data.messageType === 'success') {
                this.dispatchEvent(new CustomEvent('eligibility-checked', {
                    detail: {
                        eligibility: data.eligibility,
                        coverage: data.coverage
                    },
                    bubbles: true,
                    composed: true
                }));
            } else {
                throw new Error(data.message || 'Failed to check eligibility');
            }
        } catch (error) {
            console.error('Error checking eligibility:', error);
            this.error = error.message;
            this.dispatchEvent(new CustomEvent('show-notification', {
                detail: { message: error.message, type: 'error' },
                bubbles: true,
                composed: true
            }));
        } finally {
            this.isLoading = false;
        }
    }

    getStatusClass(status) {
        switch (status.toLowerCase()) {
            case 'active':
            case 'covered':
                return 'success-state';
            case 'inactive':
            case 'not_covered':
                return 'error-state';
            case 'pending':
            case 'partial':
                return 'warning-state';
            default:
                return '';
        }
    }

    getStatusIcon(status) {
        switch (status.toLowerCase()) {
            case 'active':
            case 'covered':
                return '';
            case 'inactive':
            case 'not_covered':
                return '';
            case 'pending':
            case 'partial':
                return '';
            default:
                return '';
        }
    }

    getStatusTitle(status) {
        switch (status.toLowerCase()) {
            case 'active':
            case 'covered':
                return 'Coverage Active';
            case 'inactive':
            case 'not_covered':
                return 'Coverage Inactive';
            case 'pending':
            case 'partial':
                return 'Partial Coverage';
            default:
                return 'Unknown Status';
        }
    }

    formatDate(dateString) {
        if (!dateString) return 'N/A';
        const date = new Date(dateString);
        return date.toLocaleDateString('en-US', {
            year: 'numeric',
            month: 'short',
            day: 'numeric'
        });
    }
} 