import { LitElement, html, css } from 'lit';
import { customElement } from 'lit/decorators.js';
import { API_ENDPOINTS } from '../../config/api';
import { sharedStyles } from './shared-styles';

@customElement('visit-section')
export class VisitSection extends LitElement {
    static get properties() {
        return {
            selectedVisit: { type: Object },
            visitType: { type: String },
            visitSearchResults: { type: Array, state: true },
            showVisitResults: { type: Boolean, state: true },
            isLoading: { type: Boolean, state: true }
        };
    }

    constructor() {
        super();
        this.selectedVisit = null;
        this.visitType = 'outpatient';
        this.visitSearchResults = [];
        this.showVisitResults = false;
        this.isLoading = false;
    }

    static get styles() {
        return [
            sharedStyles,
            css`
                :host {
                    display: block;
                }

                .visit-details-grid {
                    display: grid;
                    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
                    gap: 1rem;
                    margin-top: 1rem;
                }

                .visit-type-selector {
                    display: flex;
                    gap: 1rem;
                    margin-bottom: 1.5rem;
                    flex-wrap: wrap;
                }

                .visit-type-option {
                    background: var(--gray-50);
                    border: 2px solid var(--gray-200);
                    border-radius: 8px;
                    padding: 1rem;
                    cursor: pointer;
                    transition: all 0.2s ease;
                    flex: 1;
                    min-width: 200px;
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;
                }

                .visit-type-option:hover {
                    border-color: var(--primary-light);
                    background: white;
                }

                .visit-type-option.selected {
                    border-color: var(--primary);
                    background: white;
                    box-shadow: 0 2px 4px rgba(133, 0, 216, 0.1);
                }

                .visit-type-icon {
                    width: 24px;
                    height: 24px;
                    color: var(--primary);
                }

                .visit-type-content {
                    flex: 1;
                }

                .visit-type-title {
                    font-weight: 500;
                    color: var(--gray-900);
                    margin-bottom: 0.25rem;
                }

                .visit-type-description {
                    font-size: 0.875rem;
                    color: var(--gray-600);
                }

                .search-results {
                    margin-top: 0.5rem;
                }

                .search-result-item {
                    display: grid;
                    grid-template-columns: auto 1fr auto;
                    gap: 1rem;
                    align-items: center;
                }

                .visit-info {
                    display: flex;
                    flex-direction: column;
                    gap: 0.25rem;
                }

                .visit-name {
                    font-weight: 500;
                    color: var(--gray-900);
                }

                .visit-meta {
                    display: flex;
                    gap: 1rem;
                    color: var(--gray-600);
                    font-size: 0.875rem;
                }

                .meta-item {
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;
                }

                .meta-icon {
                    width: 16px;
                    height: 16px;
                    color: var(--gray-400);
                }

                .visit-status {
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;
                }

                .status-indicator {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                }

                .status-active {
                    background-color: var(--success);
                }

                .status-pending {
                    background-color: var(--warning);
                }

                .status-inactive {
                    background-color: var(--error);
                }
            `
        ];
    }

    render() {
        return html`
            <div class="form-section">
                <h2 class="section-title">Visit Information</h2>
                
                <div class="visit-type-selector">
                    <div class="visit-type-option ${this.visitType === 'outpatient' ? 'selected' : ''}"
                         @click=${() => this.selectVisitType('outpatient')}>
                        <div class="visit-type-icon"></div>
                        <div class="visit-type-content">
                            <div class="visit-type-title">Outpatient</div>
                            <div class="visit-type-description">Outpatient visit</div>
                        </div>
                    </div>
                    <div class="visit-type-option ${this.visitType === 'inpatient' ? 'selected' : ''}"
                         @click=${() => this.selectVisitType('inpatient')}>
                        <div class="visit-type-icon"></div>
                        <div class="visit-type-content">
                            <div class="visit-type-title">Inpatient</div>
                            <div class="visit-type-description">Inpatient visit</div>
                        </div>
                    </div>
                    <div class="visit-type-option ${this.visitType === 'emergency' ? 'selected' : ''}"
                         @click=${() => this.selectVisitType('emergency')}>
                        <div class="visit-type-icon"></div>
                        <div class="visit-type-content">
                            <div class="visit-type-title">Emergency</div>
                            <div class="visit-type-description">Emergency visit</div>
                        </div>
                    </div>
                </div>

                <div class="search-section">
                    <div class="form-group">
                        <label class="form-label">Search Visits</label>
                        <div class="search-container">
                            <input 
                                type="text" 
                                class="form-control" 
                                placeholder="Search by visit ID or date..."
                                @input=${this.handleVisitSearch}
                                @focus=${() => this.showVisitResults = true}
                            >
                            ${this.isLoading ? html`<div class="loader"></div>` : ''}
                            ${this.showVisitResults ? html`
                                <div class="search-results visit-search-results">
                                    ${this.isLoading ? html`
                                        <div class="search-status">Searching...</div>
                                    ` : this.visitSearchResults && this.visitSearchResults.length > 0 ? html`
                                        ${this.visitSearchResults.map(visit => html`
                                            <div class="visit-card ${this.selectedVisit?.id === visit.id ? 'selected' : ''}"
                                                 @click=${() => this.selectVisit(visit)}>
                                                <div class="visit-header">
                                                    <span class="visit-title">Visit #${visit.visitNumber}</span>
                                                    <span class="visit-date">${this.formatDate(visit.visitDate)}</span>
                                                </div>
                                                <div class="visit-info">
                                                    <div>Department: ${visit.department}</div>
                                                    <div>Doctor: ${visit.doctorName}</div>
                                                    <div>
                                                        Status: 
                                                        <span class="visit-status status-${visit.status.toLowerCase()}">
                                                            ${visit.status}
                                                        </span>
                                                    </div>
                                                </div>
                                            </div>
                                        `)}
                                    ` : html`
                                        <div class="search-status">No visits found</div>
                                    `}
                                </div>
                            ` : ''}
                        </div>
                    </div>
                </div>

                ${this.selectedVisit ? html`
                    <div class="visit-details-grid">
                        <div class="form-group">
                            <label class="form-label">Visit Number</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.visitNumber} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Visit Date</label>
                            <input type="text" class="form-control" .value=${this.formatDate(this.selectedVisit.visitDate)} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Department</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.department} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Doctor</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.doctorName} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Status</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.status} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Visit Type</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.visitType} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Facility</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.facility} readonly>
                        </div>
                        <div class="form-group">
                            <label class="form-label">Room/Ward</label>
                            <input type="text" class="form-control" .value=${this.selectedVisit.roomWard || 'N/A'} readonly>
                        </div>
                    </div>
                ` : ''}
            </div>
        `;
    }

    selectVisitType(type) {
        this.visitType = type;
        this.dispatchEvent(new CustomEvent('visit-type-changed', {
            detail: { visitType: type },
            bubbles: true,
            composed: true
        }));
    }

    async handleVisitSearch(e) {
        const searchTerm = e.target.value.trim();
        
        if (searchTerm.length < 2) {
            this.visitSearchResults = [];
            this.showVisitResults = false;
            return;
        }

        this.isLoading = true;
        this.showVisitResults = true;

        try {
            const response = await fetch(`${API_ENDPOINTS.VISIT.SEARCH}?term=${encodeURIComponent(searchTerm)}&type=${this.visitType}`);
            if (!response.ok) throw new Error('Failed to fetch visits');
            
            const data = await response.json();
            
            if (data.messageType === 'success' && Array.isArray(data.dynamicResult)) {
                this.visitSearchResults = data.dynamicResult;
            } else {
                this.visitSearchResults = [];
                console.error('No results in visit search response');
            }
        } catch (error) {
            console.error('Error searching visits:', error);
            this.dispatchEvent(new CustomEvent('show-notification', {
                detail: { message: 'Error searching visits', type: 'error' },
                bubbles: true,
                composed: true
            }));
            this.visitSearchResults = [];
        } finally {
            this.isLoading = false;
        }
    }

    selectVisit(visit) {
        this.dispatchEvent(new CustomEvent('visit-selected', {
            detail: visit,
            bubbles: true,
            composed: true
        }));
        this.showVisitResults = false;
        this.visitSearchResults = [];
    }

    formatDate(dateString) {
        if (!dateString) return '';
        const date = new Date(dateString);
        return date.toLocaleDateString('en-US', {
            year: 'numeric',
            month: 'short',
            day: 'numeric',
            hour: '2-digit',
            minute: '2-digit'
        });
    }
} 